import React from "react";

const ButtonCondition5 = () => {

    const [currentClick, setCurrentClick] = React.useState(null);
    const [prevClick, setPrevClick] = React.useState(null);
    
    const GetClick = (e) => {
      setCurrentClick(e.target.id);
      console.log(e.target.id);
    };
    
    React.useEffect(
        (e) => {
          if (currentClick !== null) {
            let current = document.getElementById(currentClick);
            console.log(current);
            current.style.color = "blue";
          }
    
          if (prevClick !== null) {
            let prev = document.getElementById(prevClick);
            prev.style.color = "black";
          }
          setPrevClick(currentClick);
        },
        [currentClick]
      );
    
      return (
        <React.Fragment>
            <center><b>검색조건5</b>&nbsp;&nbsp;
              <button id="case18" onClick={GetClick}>
              분류1
              </button>
              <button id="case19" onClick={GetClick}>
              분류2
              </button>
              <button id="case20" onClick={GetClick}>
              분류3
              </button>
              <button id="case21" onClick={GetClick}>
              분류4
              </button>
              </center><br></br>
        </React.Fragment>
      );
    };

    export default ButtonCondition5;
