import React from "react";

const ButtonPrice = () => {

    const [currentClick, setCurrentClick] = React.useState(null);
    const [prevClick, setPrevClick] = React.useState(null);
    
    const GetClick = (e) => {
      setCurrentClick(e.target.id);
      console.log(e.target.id);
    };
    
    React.useEffect(
        (e) => {
          if (currentClick !== null) {
            let current = document.getElementById(currentClick);
            console.log(current);
            current.style.color = "blue";
          }
    
          if (prevClick !== null) {
            let prev = document.getElementById(prevClick);
            prev.style.color = "black";
          }
          setPrevClick(currentClick);
        },
        [currentClick]
      );
    
      return (
        <React.Fragment>
            <center><b>상태</b>&nbsp;&nbsp;
              <button id="case5" onClick={GetClick}>
              ~10만원
              </button>
              <button id="case6" onClick={GetClick}>
              ~30만원
              </button>
              <button id="case7" onClick={GetClick}>
              ~50만원
              </button>
              <button id="case8" onClick={GetClick}>
              ~100만원
              </button>
              <button id="case9" onClick={GetClick}>
                전체
              </button>
              </center><br></br>
        </React.Fragment>
      );
    };

    export default ButtonPrice;
